import React, { useRef } from "react";

type MenuItem = {
  id: number;
  name: string;
  description: string;
  price: number;
  category?: string;
  image?: string;
};

const coffeeItems: MenuItem[] = [
  {
    id: 1,
    name: "Lungo Coffee",
    description: "An espresso coffee drink topped with a small foam layer",
    price: 199,
    category: "coffee",
    image:
      "https://images.unsplash.com/photo-1511920170033-f8396924c348?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 2,
    name: "Dalgona Coffee",
    description: "Whipped coffee made using instant coffee and milk",
    price: 159,
    category: "coffee",
    image:
      "https://images.unsplash.com/photo-1600891964486-6b3b0b39a2f0?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 3,
    name: "Iced Coffee",
    description: "Cold brewed coffee served with ice and milk",
    price: 149,
    category: "coffee",
    image:
      "https://images.unsplash.com/photo-1541167760496-1628856ab772?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 4,
    name: "Filter Coffee",
    description: "Classic filter coffee, freshly brewed",
    price: 59,
    category: "coffee",
    image:
      "https://images.unsplash.com/photo-1509042239860-f550ce710b93?auto=format&fit=crop&w=800&q=60",
  },
];

const dessertItems: MenuItem[] = [
  {
    id: 101,
    name: "Gulab Jamun",
    description: "Milk-based sweet soaked in sugar syrup",
    price: 199,
    category: "dessert",
    image:
      "https://images.unsplash.com/photo-1563805042-7684c019e1cb?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 102,
    name: "Chocolate Tiramisu",
    description: "Layers of mascarpone cream & chocolate",
    price: 250,
    category: "dessert",
    image:
      "https://images.unsplash.com/photo-1604908177522-02b9ea2d9d0b?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 103,
    name: "Churros",
    description: "Fried dough coated with sugar & cinnamon",
    price: 170,
    category: "dessert",
    image:
      "https://images.unsplash.com/photo-1546539787-0e4b3b8f7b82?auto=format&fit=crop&w=800&q=60",
  },
  {
    id: 104,
    name: "Lamingtons",
    description: "Sponge cake coated in chocolate & coconut",
    price: 280,
    category: "dessert",
    image:
      "https://images.unsplash.com/photo-1578985545062-69928b1d9587?auto=format&fit=crop&w=800&q=60",
  },
];

export default function Menu({ onAddToCart }: { onAddToCart: (item: MenuItem) => void }) {
  const coffeeRef = useRef<HTMLDivElement | null>(null);
  const dessertRef = useRef<HTMLDivElement | null>(null);

  const scroll = (ref: React.RefObject<HTMLDivElement>, dir: "left" | "right") => {
    if (!ref.current) return;
    const amount = ref.current.clientWidth * 0.8;
    ref.current.scrollBy({ left: dir === "left" ? -amount : amount, behavior: "smooth" });
  };

  const handleAdd = (item: MenuItem) => {
    // send to parent (expected to update UI state)
    onAddToCart(item);

    // sync localStorage (persist cart across pages)
    const existing = JSON.parse(localStorage.getItem("cart") || "[]");
    existing.push(item);
    localStorage.setItem("cart", JSON.stringify(existing));

    // show non-blocking toast
    showToast(`${item.name} added to cart`);
  };

  // simple toast system — creates ephemeral element and removes after timeout
  const showToast = (message: string) => {
    const el = document.createElement("div");
    el.textContent = message + " ✅";
    el.className =
      "fixed right-6 bottom-6 bg-gray-900 text-white px-4 py-2 rounded-md shadow-lg z-[9999] animate-fade-in-out";
    document.body.appendChild(el);
    setTimeout(() => el.remove(), 2200);
  };

  return (
    <div className="bg-[#efe6db] min-h-screen pb-16">
      {/* HERO */}
      <section
        className="relative h-[420px] md:h-[380px] lg:h-[420px] flex items-center justify-center bg-cover bg-center"
        style={{
          backgroundImage:
            "url('/mnt/data/d6642a9fe6cca36b0f65c6240852ad51.jpg')",
        }}
      >
        <div className="absolute inset-0 bg-black/40" />
        <div className="relative max-w-5xl mx-6 md:mx-0 text-center text-white">
          <p className="text-sm uppercase tracking-widest mb-2">Welcome!</p>
          <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold leading-tight">
            We serve the richest coffee in the city!
          </h1>
          <p className="mt-4 text-sm md:text-base max-w-2xl mx-auto">
            Freshly brewed beans, handcrafted beverages and delightful desserts — visit us or order
            online.
          </p>
          <div className="mt-6 flex items-center justify-center gap-4">
            <button className="bg-white text-[#3b2b20] font-semibold px-6 py-2 rounded-full shadow hover:scale-[1.02] transition">
              Order Now
            </button>
            <button className="border border-white text-white px-4 py-2 rounded-full hover:bg-white/10 transition">
              Explore Menu
            </button>
          </div>
        </div>
      </section>

      {/* Categories */}
      <section className="max-w-6xl mx-auto px-6 mt-8">
        <div className="flex justify-center items-center gap-10 text-[#3b2b20]">
          <Category icon="☕" label="Hot Coffee" />
          <Category icon="🥤" label="Cold Coffee" />
          <Category icon="🍵" label="Cup Coffee" />
          <Category icon="🍰" label="Dessert" />
        </div>
      </section>

      {/* Specials - Coffee */}
      <section className="max-w-6xl mx-auto px-6 mt-10">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-2xl font-bold text-[#3b2b20]">Our Special Coffee</h2>
          <div className="flex gap-2">
            <button
              aria-label="scroll coffee left"
              onClick={() => scroll(coffeeRef, "left")}
              className="w-10 h-10 rounded-full bg-white shadow flex items-center justify-center"
            >
              ‹
            </button>
            <button
              aria-label="scroll coffee right"
              onClick={() => scroll(coffeeRef, "right")}
              className="w-10 h-10 rounded-full bg-white shadow flex items-center justify-center"
            >
              ›
            </button>
          </div>
        </div>

        <div
          ref={coffeeRef}
          className="relative flex gap-6 overflow-x-auto no-scrollbar pb-4"
          style={{ scrollSnapType: "x mandatory" }}
        >
          {coffeeItems.map((item) => (
            <Card key={item.id} item={item} onAdd={handleAdd} />
          ))}
        </div>
      </section>

      {/* Specials - Dessert */}
      <section className="max-w-6xl mx-auto px-6 mt-8">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-2xl font-bold text-[#3b2b20]">Our Special Dessert</h2>
          <div className="flex gap-2">
            <button
              aria-label="scroll dessert left"
              onClick={() => scroll(dessertRef, "left")}
              className="w-10 h-10 rounded-full bg-white shadow flex items-center justify-center"
            >
              ‹
            </button>
            <button
              aria-label="scroll dessert right"
              onClick={() => scroll(dessertRef, "right")}
              className="w-10 h-10 rounded-full bg-white shadow flex items-center justify-center"
            >
              ›
            </button>
          </div>
        </div>

        <div
          ref={dessertRef}
          className="relative flex gap-6 overflow-x-auto no-scrollbar pb-4"
          style={{ scrollSnapType: "x mandatory" }}
        >
          {dessertItems.map((item) => (
            <Card key={item.id} item={item} onAdd={handleAdd} />
          ))}
        </div>
      </section>

      {/* small footer spacer */}
      <div className="max-w-6xl mx-auto px-6 mt-12">
        <div className="bg-white rounded-lg shadow p-6 text-gray-700">
          <p className="text-center">Visit our cafe or order online. Fresh beans daily ☕</p>
        </div>
      </div>

      <style jsx>{`
        .no-scrollbar {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .no-scrollbar::-webkit-scrollbar {
          display: none;
        }
        @keyframes fadeInOut {
          0% {
            opacity: 0;
            transform: translateY(6px);
          }
          10% {
            opacity: 1;
            transform: translateY(0);
          }
          90% {
            opacity: 1;
            transform: translateY(0);
          }
          100% {
            opacity: 0;
            transform: translateY(-6px);
          }
        }
        .animate-fade-in-out {
          animation: fadeInOut 2.2s ease-in-out forwards;
        }
      `}</style>
    </div>
  );
}

/* helper components */
function Category({ icon, label }: { icon: string; label: string }) {
  return (
    <div className="flex flex-col items-center gap-2">
      <div className="w-14 h-14 rounded-full bg-white shadow flex items-center justify-center text-2xl">
        {icon}
      </div>
      <p className="text-sm font-medium text-[#3b2b20]">{label}</p>
    </div>
  );
}

function Card({ item, onAdd }: { item: MenuItem; onAdd: (i: MenuItem) => void }) {
  return (
    <article
      className="min-w-[260px] max-w-[320px] bg-white rounded-xl shadow-lg overflow-hidden flex-shrink-0"
      style={{ scrollSnapAlign: "center" }}
    >
      <div className="relative">
        <img src={item.image} alt={item.name} className="w-full h-48 object-cover" />
        <button
          aria-label="favorite"
          className="absolute top-3 right-3 bg-white/80 p-2 rounded-full shadow"
        >
          ♥
        </button>
        <div className="absolute left-3 bottom-3 bg-white/90 px-3 py-1 rounded-full text-sm font-semibold shadow">
          ₹{item.price}
        </div>
      </div>

      <div className="p-4 flex flex-col h-[150px]">
        <div className="flex items-start justify-between">
          <h3 className="text-lg font-semibold text-[#3b2b20]">{item.name}</h3>
        </div>
        <p className="text-sm text-gray-600 mt-2 flex-1">{item.description}</p>

        <div className="mt-4 flex items-center justify-between">
          <button
            onClick={() => onAdd(item)}
            className="bg-[#3b2b20] hover:bg-[#2e2318] text-white px-4 py-2 rounded-md text-sm font-medium"
          >
            Order Now
          </button>
          <button
            className="text-sm px-2 py-1 border rounded-md text-gray-600 hover:bg-gray-100"
            onClick={() => {
              alert(`${item.name}\n\n${item.description}\n\nPrice: ₹${item.price}`);
            }}
          >
            Details
          </button>
        </div>
      </div>
    </article>
  );
}
